.details-card {
  justify-content: space-between;
  display: flex;
  max-width: 600px;
  padding: 24px;
  border: 1px solid red;
  border-radius: 12px;
  margin: 0 auto;
  height: 600px;
}
.light-link {
  font-size: 0.6em;
  color: grey;
  text-decoration: underline;
}
.details-back {
  display: flex;
  align-items: center;
  gap: 8px;
}
